			<text class="text-item">【特别提示】请您仔细阅读我们的《回收猿隐私政策》并确定了解我们对您个人信息的处理规则。阅读过程中，如您有任何疑问，可及时与我们联系（联系方式以隐私政策里约定的为准）。如您不同意协议中的任何条款，您应立即停止回收猿相关产品。
