			<text class="text-item">回收猿（以下亦称“我们”）深知个人信息对您的重要性，我们尊重并保护所有使用回收猿平台服务的用户的个人信息，并会尽全力保护您的个人信息安全可靠。同时，我们承诺，我们将按业界成熟的安全标准，采取相应的安全保护措施来保护您的个人信息。
				请在使用我们的产品（或服务）前，仔细阅读并了解本《回收猿隐私政策》（下称“本隐私政策”）。</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">如您对本隐私政策有任何疑问或您在使用我们提供的服务时个人信息受到了侵扰，您可以通过回收猿网站（www.jiadianhuishou.com）首页下方的违法信息举报方式与我们及时取得联系，办公地址：湛江开发区绿华路48号华都汇7号综合楼1108号办公室。
			</text>
