			<text class="text-item">&nbsp;</text>
			<text class="text-item">一、我们处理个人信息的总原则</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">通常，我们会基于本隐私政策提示的功能收集您的个人信息。某些情况下，如果涉及其他信息的收集我们会单独向您出示个人信息保护说明条款。在处理您的个人信息方面，我们致力于维持您对我们的信任，恪守以下原则，保护您的个人信息：权责一致原则、目的明确原则、选择同意原则、最少够用原则、确保安全原则、主体参与原则、公开透明原则。
			</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">二、定义及本隐私政策的适用范围</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">1、回收猿：指回收猿网站www.jiadianhuishou.com及回收猿客户端。</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">2、回收猿服务提供者：指回收猿的互联网信息及软件技术服务提供者湛江市致尚网络科技有限公司。</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">3、个人信息：指以电子或者其他方式记录的能够单独或者与其他信息结合识别特定自然人身份或者反映特定自然人活动情况的各种信息。</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">4、个人敏感信息：指包括身份证件号码、个人生物识别信息、银行账号、财产信息、行踪轨迹、交易信息、14岁以下（含）儿童信息等个人信息（我们将在本政策中对具体个人敏感信息以粗体进行显著标识）。</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">5、个人信息删除：指在实现日常业务功能所涉及的系统中去除个人信息的行为时期保持不可被检索、访问的状态。</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">6、儿童：指不满14周岁的未成年人。</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">7、本隐私政策适用于您使用回收猿（包括回收猿网站、回收猿无线站点、回收猿移动应用软件回收猿APP）的产品或服务时使用。</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">三、我们如何收集和使用您的个人信息</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">本隐私政策所涉及的个人信息或个人敏感信息所包含的内容出自于《个人信息安全规范》。我们将在本条第（一）、第（二）款中详细论述我们基于以下所述功能，收集和使用您的个人信息的类型和目的。</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">个人信息是指以电子或者其他方式记录的能够单独或者与其他信息结合识别特定自然人身份或者反映特定自然人活动情况的各种信息，如姓名、出生日期、身份证件号码、个人生物识别信息、住址、通信通讯联系方式、通信记录和内容、账号密码、财产信息、征信信息、行踪轨迹、住宿信息、健康生理信息、交易信息等。
			</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">个人敏感信息是指一旦泄露、非法提供或滥用可能危害人身和财产安全，极易导致个人名誉、身心健康受到损害或歧视性待遇等的个人信息。本隐私政策涉及到的个人敏感信息我们将加粗并加下划线的方式提示您注意阅读。在您向回收猿提供任何属于敏感信息的个人信息前，请您清楚考虑该等提供是恰当的并且同意您的个人敏感信息可按本隐私政策所述的目的和方式进行处理。我们会在得到您的同意后收集和使用您的敏感信息以实现与回收猿业务相关的功能，并允许您对这些敏感信息的收集与使用做出不同意的选择，但是拒绝使用这些信息会影响您使用相关功能。
			</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">原则上，回收猿仅会出于本隐私政策所述的以下目的，收集和使用您的个人信息。回收猿是综合类的信息发布平台，所涉场景较多，如果超过以下目的收集和使用您的个人信息时我们会单独向您提示并征得您的同意。</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">（一）帮助您成为我们的注册\登录用户</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">1、您自行注册成为回收猿的用户</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">您在使用回收猿提供的服务时，首先需要成为我们的注册\登录用户。根据《中华人民共和国电子商务法》第二十七条的规定“电子商务平台经营者应当要求申请进入平台销售商品或者提供服务的经营者提交其身份、地址、联系方式、行政许可等真实信息，进行核验、登记，建立登记档案，并定期核验更新。”因此，当您注册回收猿账户时，您需要向我们提供您准备使用的用户名及您本人的手机号码，我们将通过发送短信验证码的方式来验证您的身份是否有效。您登录后可以继续完善您的账户信息，您可以修改补充您的昵称、所在地、家乡、职业、职位、兴趣爱好；您使用交友服务时，可以根据自己需要上传的照片。您补充的账户信息将有助于我们为您提供个性化的服务推荐和更优的服务体验，包括：提升账号的安全性，以便您享受更好的保障服务；提升账号的信用度，用于核实真实身份；可以开通VIP会员，享受专属服务。但如果您不提供这些补充信息，不会影响您使用回收猿的基本功能。
			</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">2、您通过第三方授权登录为回收猿的用户</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">您使用第三方帐号（微信）登录回收猿时，可选择授权回收猿在符合相关法律法规要求的前提下读取并获得您在该第三方平台上登记、公布、记录的公开信息（包括昵称、头像）。</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">回收猿需要您授权从第三方获取上述信息是为了记住您作为回收猿用户的登录身份，以向您提供更优质的产品和/或服务。我们仅会在您的授权同意的范围内收集并使用您的个人信息。</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">您提供的上述信息，将在您使用回收猿服务期间持续授权我们使用。在您注销账号时，我们将停止使用并删除上述信息。</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">上述信息将存储于中华人民共和国境内。如需跨境传输，我们将会单独征得您的授权同意。</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">（二）向您提供我们的产品或服务</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">1、信息发布功能</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">回收猿平台最主要的功能就是信息发布功能，您可以根据您的需要选择发布信息的种类。当您使用发布功能时，我们会收集您的个人信息（信息品类不同，收集的信息不一样），包括姓名、联系方式、个人信息资料、地理位置、用户相册、邮箱、地址信息。此类信息为您自己主动填写的信息，收集是为了方便您能成功发布信息，可以更快捷的与您发布的内容进行匹配。
			</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">2、搜索功能</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">当使用回收猿提供的搜索功能时，我们会收集您查询的关键字信息以及您在使用回收猿服务时所浏览或要求的其他信息和内容详情。为了给您带来更便捷的搜索服务并不断完善回收猿产品和/或服务。该等关键词信息通常无法单独识别您的个人身份，不属于您的个人信息，不在本隐私政策的限制范围内。只有当您的搜索关键词信息与您的其他信息有联结并可识别您的个人身份时，则在结合使用期间，我们会将您的搜索关键词信息作为您的个人信息，与您的搜索历史记录一同按照本隐私政策对其进行处理与保护。
			</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">3、身份认证服务</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">回收猿提供身份认证服务，在对您个人信息的真实性进行审核后，会为您标记认证标识。
				因此，当您申请回收猿为您进行身份认证时，我们需要收集您的姓名、身份证号、银行卡、有关身份证明的信息，以证实您的个人身份相关信息，具体验证的方式以“个人信息”—“我的认证”显示的为准，您可以根据需要自行选择认证的方式。我们还可能需要收集您的生物识别信息，用于人脸验证。例如，您提交个人认证，需要提供您的身份证信息，以便我们进行核实。
			</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">4、客户服务</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">当您向回收猿发布信息、申诉或进行咨询时，为了方便与您联系或帮助您解决问题，我们可能需要您提供姓名、手机号码、电子邮件信息。如您拒绝提供上述信息，可能部分功能无法使用，同时无法向您及时反馈申诉或咨询结果。</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">5、定向推送</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">我们会基于收集的信息，对您的偏好、习惯、位置作特征分析和用户画像，以便为您提供更适合的定制化服务，例如向您展现或推荐相关程度更高（而非普遍推送）的订阅信息、信息流或者广告/推广信息结果。为此，我们需要收集的信息包括您的设备信息、日志信息、浏览记录。如您不希望获得我们的定向推送服务，回收猿APP端，您可以通过“我的”-“更多”—“消息管理”中进行设置。
			</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">6、营销活动</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">我们将会不时举办线上或线下的营销活动，包括邀请用户奖励红包、赠送实体物品活动。在此类营销活动中，我们可能需要用户提供个人信息（如手机号、邮编、地址信息），以便我们能够给用户发放奖励或寄送礼品。在涉及回收猿向您支付费用的情况下，我们可能需要收集您的身份信息（姓名、身份证号）用于代缴税款。
			</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">7、支付/提现功能</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">您可在回收猿进行部分服务支付购买。在您使用该服务的过程中可能会需要进行支付，在支付过程中， 我们可能会收集您的第三方支付帐号(例如支付宝帐号、Apple Pay 帐号或其他形式的银行卡信息)。</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">您在回收猿参与一些推广活动获得回收猿给予的奖励，您在提现时，基于国家税务部门的要求，我们会代扣代缴个税，此时会按照税务部门的要求收集您的身份信息（姓名、身份证号）。</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">8、为您提供会员或订阅服务</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">您在回收猿平台购买会员或自动订阅服务支付费用时，我们会记录您的支付方式，以完成支付服务。</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">9、客户服务及争议处理</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">为向您提供客户服务，我们会记录您提供的联系方式和通信/通话记录和内容，用于与您联系和帮助您解决问题，或记录相关问题的处理方案及结果。</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">10、为您提供更个性化的服务</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">1）基于位置信息的个性化推荐功能：我们会收集您的位置信息（我们仅收集您当时所处的地理位置，但不会将您各时段的位置信息进行结合以判断您的行踪轨迹）来判断您所处的地点。如房产频道，会自动为您推荐您所在区域的房源或楼盘信息。
			</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">2）基于摄像头（相机）的服务功能：您可以使用这个功能完成视频拍摄、拍照、推广信息的编辑发布，或通过该功能完成推广信息的查阅。</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">3）基于图片上传的功能：您可以在回收猿上传您的照片来实现您想要发布的推广信息的编辑发布。</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">4）基于语音技术的附加功能：您可以直接使用麦克风来发布信息或与回收猿的其他用户进行非实时的互动。</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">上述附加功能可能需要您在您的设备中向我们开启您的地理位置（位置信息）、相机（摄像头）、相册（图片库）、麦克风，以实现这些功能所涉及的信息的收集和使用。我们将在您首次下载APP或者具体使用到上述所列功能时逐一以弹窗的形式提前向您提示，您可以根据您的需要进行选择。请您注意，您开启这些权限即代表您授权我们可以收集和使用这些个人信息来实现上述的功能，您关闭权限即代表您取消了这些授权，则我们将不再继续收集和使用您的这些个人信息，也无法为您提供上述与这些授权所对应的功能。您关闭权限的决定不会影响此前基于您的授权所进行的个人信息的处理。如您不同意使用回收猿的个性化推荐服务，您可在“回收猿APP-我的-设置-隐私管理-接收个性化推荐”的位置进行关闭此服务。您亦可在“回收猿APP-我的-设置-消息管理”的位置进行关闭“系统消息”、“热门推荐”服务。
			</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">11、其他服务</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">1）基于不同的业务场景，我们有时会接入第三方SDK：如微信SDK,在获得您同意的情况下，获取您的微信账号信息，进行回收猿账号的登录、绑定、信息分享、支付功能；百度地图SDK，收集您的位置信息，应用于与位置相关的业务场景；
				2、应用列表信息：我们采用合并链路技术进行技术推送，当一个设备有多个APP的推送链路同时活跃时，会随机合并成一条链路，以达到为用户省电省流量的目的，因此需获得应用列表信息。3、网络信息以及位置相关信息：为了最大程度保持网络连接的稳定性，建立长链接，需要了解设备的网络状态和变化，从而实现稳定连续的推送服务；为提供应景推送功能，位置相关信息将有助于为您提供线下场景的精细化推送，可以为您推荐更符合您需要的推送内容，减少无用推送消息对您的打扰。）；
			</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">2）基于二次验证您账号身份的场景，我们有时会收集您的常用手机品牌。当回收猿系统发现您的账号信息疑似出现登录异常时，我们会向您进行问题调研，结合您填写答案对您的账号进行匹配验证，以确保您账号的使用安全。除此之外，回收猿不对上述收集的信息进行其他形式的使用。
			</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">3）除上述回收猿向您提供的产品和服务外，我们可能会收集您的MAC地址信息、软件列表信息、imei或oaid、Android Id 、CPU ID 序列号、OpenUUId (iOS
				)、网络地址，该信息的收集用于保障您的回收猿账号安全、防盗号、信息安全的功能。</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">4）我们可能为了提供服务及改进服务质量的合理需要而收集您的其他信息，包括您与我们的客户服务团队联系时所提供的相关信息，您参与问卷调查时向我们发送的问卷答复信息。与此同时，为提高您使用回收猿平台提供的服务的安全性，更准确地预防钓鱼网站欺诈和木马病毒，我们可能会通过了解一些您的网络使用习惯、您常用的软件信息手段来判断您账户的风险，并可能会记录一些我们认为有风险的URL。
			</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">在不透露单个用户隐私资料的前提下，回收猿及其关联公司有权对整个用户数据库进行分析并对用户数据库进行商业上的利用。回收猿可能会对网络服务使用情况进行统计。同时，回收猿可能会与公众分享这些统计信息，以展示我们服务的整体使用趋势。这些统计信息不包含您的任何身份识别信息。
			</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">（三）征得授权同意的例外</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">根据相关法律法规的规定，在以下情形中，我们可以在不征得您的授权同意的情况下收集、使用一些必要的个人信息：</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">1、与国家安全、国防安全直接相关的；</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">2、与公共安全、公共卫生、重大公共利益直接相关的；</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">3、与犯罪侦查、起诉、审判和判决执行等直接相关的；</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">4、出于维护您或其他个人的生命、财产等重大合法权益但又很难得到本人同意的；</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">5、所收集的个人信息是您自行向社会公众公开的；</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">6、从合法公开披露的信息中收集到您的个人信息，如从合法的新闻报道、政府信息公开等渠道；</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">7、根据您的要求签订和履行合同所必需的；</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">8、法律法规规定的其他情形。</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">您知悉并认可：回收猿通过广告或其他方式向您提供链接，使您可以接入第三方服务或网站。您使用该等第三方的服务时，须受该第三方的服务条款及隐私政策约束，回收猿提示您需要仔细阅读其政策。本协议仅适用于回收猿提供的服务器所收集的信息，并不适用于第三方提供的服务或第三方的信息使用的规则，回收猿对第三方使用由您自行提供的信息不承担责任。
			</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">四、我们如何使用 Cookie 和同类技术</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">为使您获得更轻松的访问体验，您访问回收猿平台相关网站或使用回收猿平台提供的服务时，我们可能会通过小型数据文件识别您的身份，这么做是帮您省去重复输入注册信息的步骤，或者帮助判断您的账户安全。这些数据文件可能是Cookie，Flash
				Cookie，或您的浏览器提供的其他本地存储（统称“Cookie”）。</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">网页上常会包含一些电子图象（称为“单像素” GIF 文件或 “网络
				beacon”），使用网络beacon可以帮助网站计算浏览网页的用户或访问某些cookie，我们会通过网络beacon收集您浏览网页活动的信息，例如您访问的页面地址、您先前访问的援引页面的位址、您停留在页面的时间、您的浏览环境以及显示设定。
			</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">回收猿可能使用您在回收猿网站浏览的行为数据，根据您的兴趣喜好，在您浏览网站时有可能展现与您兴趣相关的信息或内容，这个过程包括以下几个方面：</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">1)兴趣数据的收集：我们通过您在回收猿浏览过的内容，使用Cookie收集并保存您的行为数据。</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">2)根据您的浏览行为数据，获取您的兴趣分类。</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">我们不会将上述技术收集的信息用于本隐私政策所述目的之外的任何用途。请您理解，我们的某些服务只能通过使用“Cookie”才可得到实现。如果您的浏览器或浏览器附加服务允许，您可以在相关的页面修改对Cookie的接受程度，但这一举动在某些情况下可能会影响您安全访问回收猿相关网站和使用回收猿平台提供的服务。
			</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">五、我们如何共享、转让、公开披露您的个人信息</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">（一）共享</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">我们不会与回收猿以外的任何公司、组织和个人分享您的个人信息，但以下情况除外：</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">1、在获得明确同意的情况下共享：获得您的明确同意后，我们会与其他方共享您的个人信息。</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">2、根据法律法规的规定和行政、司法机构的要求：我们可能会根据法律法规规定，或按政府主管部门的强制性要求，向上述监管部门共享您的个人信息。</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">3、与我们的关联公司共享：您的个人信息可能会与回收猿的关联公司共享。我们只会共享必要的个人信息，且受本隐私政策中所声明目的的约束。关联公司如要改变个人信息的处理目的，将再次征求您的授权同意。</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">关联公司指回收猿平台运营主体直接或间接控制的法人实体及其分支机构，与回收猿平台运营主体直接或间接共同受控于同一实际控制人的法人实体及其分支机构；控制、受控指：通过协议或股权控制或受控。</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">4、与授权合作伙伴共享：仅为实现本隐私政策中声明的目的，我们的某些服务将由授权合作伙伴提供。我们可能会与合作伙伴共享您的某些个人信息，以提供更好的客户服务和用户体验。我们仅会出于合法、正当、必要、特定、明确的目的共享您的个人信息，并且只会共享提供服务所必要的个人信息。我们的合作伙伴无权将共享的个人信息用于任何其他用途。
			</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">目前，我们的授权合作伙伴包括以下2大类型：</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">1）广告、分析服务类的授权合作伙伴。我们不会将您的个人信息与提供广告、咨询类服务商与关联公司共享，除非您主动授权我们提供。但我们可能会将经处理无法识别您的身份且接收方无法复原的信息，例如经匿名化处理的用户画像，与广告或咨询类服务商或广告主共享，以帮助其在不识别您个人的前提下，提升广告有效触达率，以及分析我们的产品和服务使用情况。
			</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">2）服务提供商和其他合作伙伴。您在使用我们的具体服务时，您自主填写的信息，我们将您填写的信息发送给您想要联系的服务提供商。</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">对我们与之共享个人信息的公司、组织和个人，我们会与其签署严格的保密协定，要求他们按照我们的说明、本隐私政策以及其他任何相关的保密和安全措施来处理个人信息。</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">5、以学术研究或公共利益为目的，且无害于您的重大利益；</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">6、如您是适格的知识产权投诉人并已提起投诉，应被投诉人要求，向被投诉人披露，以便双方处理可能的权利纠纷；</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">7、只有共享您的信息，才能提供您需要的服务或处理您与他人的纠纷或争议。例如您在回收猿上创建的某一交易中，如交易任何一方履行或部分履行了交易义务并提出信息披露请求的，回收猿会视情况向该用户提供其交易对方的联络方式必要信息，以促成交易的完成或纠纷的解决；
			</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">8、您通过点击相关产品的相应按键同意向第三方提交个人信息（包括但不限于 “帮我申请”、“帮我提交”按键）。</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">（二）转让</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">我们不会将您的个人信息转让给任何公司、组织和个人，但以下情况除外：</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">1、在获取明确同意的情况下转让：获得您的明确同意后，我们会向其他方转让您的个人信息；</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">2、在涉及合并、收购或破产清算时，如涉及到个人信息转让，我们会在要求新的持有您个人信息的公司、组织继续受此隐私政策的约束，否则我们将要求该公司、组织重新向您征求授权同意。</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">（三）公开披露</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">我们仅会在以下情况下，公开披露您的个人信息：</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">1、获得您明确同意后；</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">2、基于法律的披露：在法律、法律程序、诉讼或政府主管部门强制性要求的情况下，我们可能会对上述监管部门披露您的个人信息。</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">我们知晓对外共享、转让、公开披露个人信息所承担的相应法律责任。我们承诺按照本条款的要求共享、转让、公开披露您的个人信息，按照法律法规及国家标准的要求以确认协议、具体场景下的文案确认、弹窗、提示等形式征得您的同意或确认第三方已经征得您的同意。
			</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">因此，请您谨慎考虑通过我们的服务上传、发布和交流的信息内容。在一些情况下，您可通过我们某些服务的隐私设定来控制有权浏览您共享信息的用户范围。如要求从我们的服务中删除您的相关信息，请通过本隐私政策文首处提供的联系方式操作。
			</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">六、我们如何存储及保护您的个人信息</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">（一）信息存储</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">回收猿收集的有关您的信息和资料将保存在回收猿及（或）其关联公司的服务器上，这些信息和资料全部储存在中国境内。目前，回收猿的产品和服务不会涉及到数据的跨境传输，如果我们的产品\服务发生变更，涉及数据的跨境传输，我们会单独向您以弹窗或邮件的方式告知您数据出境的目的、接收方等，并征得您的授权同意，我们会确保数据接收方有充足的数据保护能力来保护您的个人信息。
			</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">我们仅在您使用我们的服务期间和法律法规要求的最短时限内保留您的个人信息，在仅浏览功能下所收集的个人信息如浏览记录、IP 信息，我们的存储期限不会超过1个月。对于超出期限的个人信息，我们会立即删除或做匿名化处理。
			</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">（二）信息保护</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">为保障您的信息安全，我们努力采取各种合理的物理、电子和管理方面的安全措施来保护您的信息，使您的信息不会被泄漏、毁损或者丢失，包括但不限于SSL、信息加密存储、数据中心的访问控制。</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">1、数据加密：我们对于用户的用户名、手机号、注册邮箱、qq号进行加密存储，保证用户基本信息不会被恶意获取；</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">2、身份鉴别：我们通过校验账号密码或者账号绑定手机验证码，进行用户身份合法性鉴别，防止非经授权的介入；</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">3、访问控制：我们对可能接触到您的信息的员工或外包人员也采取了严格管理，包括但不限于根据岗位的不同采取不同的权限控制，与他们签署保密协议，监控他们的操作情况等措施。回收猿用户在进行身份鉴别后，才能查询并管理用户信息和资源，未经过身份鉴别，不能管理用户资源；
			</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">4、安全审计：我们有时会接入风控平台或认证平台，对一些场景或行为进行监测。如通过流量分析，完成账号异常登陆、注册等异常行为的监控。</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">回收猿会按现有技术提供相应的安全措施来保护您的信息，提供合理的安全保障，回收猿将尽力做到使您的信息不被泄漏、毁损或丢失。</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">5、账号保护：您的账户均有安全保护功能，请妥善保管您的账户及密码信息。回收猿将通过向其它服务器备份、对用户密码进行加密等安全措施确保您的信息不丢失，不被滥用和变造。尽管有前述安全措施，但同时也请您理解，由于技术的限制以及可能存在的各种恶意手段，即便竭尽所能加强安全措施，在信息网络上也不存在“完善的安全措施”。如因您自己的原因导致账户及密码信息泄露而造成的任何法律后果需由您本人负责。
			</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">在使用回收猿平台服务进行网上交易时，如您不可避免地要向交易对方或潜在的交易对方披露自己的个人信息（例如联系人、联络方式），请您妥善保护自己的个人信息，仅在必要的情形下向他人提供。如您发现自己的个人信息已经被泄露或者存在被泄露的可能，且有可能会危及您注册获得的回收猿账户安全，或者给您造成其他的损失的，请您务必在第一时间根据本隐私政策提到的联系方式与我们取得联系，以便我们采取相应措施维护您的回收猿账户安全，防止损失的发生或者进一步扩大。
			</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">6、互联网环境并非百分之百安全，我们将尽力确保或担保您发送给我们的任何信息的安全性。在不幸发生个人信息安全事件后，我们将按照法律法规的要求，及时向您告知：安全事件的基本情况和可能的影响、我们已采取或将要采取的处置措施、您可自主防范和降低风险的建议、对您的补救措施等。我们将及时将事件相关情况以邮件、信函、电话、推送通知等方式告知您，难以逐一告知个人信息主体时，我们会采取合理、有效的方式发布公告。
			</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">七、您管理个人信息的权利</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">回收猿非常重视您对个人信息的关注，并尽全力保护您对于自己个人信息访问、更正、删除以及撤回同意的权利，以使您拥有充分的能力保障您的隐私和安全。您的权利包括：</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">（一）个人信息的查询、更正</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">您有权访问您的个人信息，法律法规规定的例外情况除外。如果您想行使数据访问权，可以通过以下方式自行访问：</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">APP端：</text>
			<text class="text-item">您可以通过首页下方“我的”—点击左上角的头像—“个人信息”访问、更改您的个人资料信息、账号信息、认证信息。</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">（二）个人信息的删除</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">除了您在上述查询、更正信息的页面进行信息删除处理外，您还可以在APP首页下方“我的”—右上角“设置”—选择“清空足迹”或“清空缓存”。在以下情形中，您可以向我们提出删除个人信息的请求：</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">1）如果我们处理个人信息的行为违反法律法规；</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">2）如果我们收集、使用您的个人信息，却未征得您的同意；</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">3）如果我们处理个人信息的行为违反了与您的约定；</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">4）如果我们终止服务及运营。</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">（三）改变您授权同意的范围或撤回您的授权</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">您可以通过删除信息、关闭设备功能、在回收猿网站或软件中进行隐私设置等方式改变您授权我们继续收集个人信息的范围或撤回您的授权。您也可以通过注销账户的方式，撤回我们继续收集您个人信息的全部授权。</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">（四）共享、转让、公开披露个人信息时事先征得授权同意的例外</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">根据法律规定，在以下情形中，我们共享、转让、公开披露您的个人信息无需事先征得您的授权同意：</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">1）与国家安全、国防安全直接相关的；</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">2）与公共安全、公共卫生、重大公共利益直接相关的；</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">3）与犯罪侦查、起诉、审判和判决执行等直接相关的；</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">4）出于维护个人信息主体或其他个人的生命、财产等重大合法权益但又很难得到本人同意的；</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">5）个人信息主体自行向社会公众公开的个人信息；</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">6）从合法公开披露的信息中收集个人信息的，如合法的新闻报道、政府信息公开等渠道。</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">（五）注销账户</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">您可以在我们的产品或服务中直接申请注销账户。关于您注销账户的方式以及您应满足的条件，请详见“注销账户”页面。
				您注销账户后，我们将停止为您提供产品与/或服务，并依据您的要求，除法律法规另有规定外，我们将删除您的个人信息。如果您对注销账号有疑，可通过回收猿APP“我的”—“联系客服”反馈，我们会有专门的人员进行回复反馈。
			</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">APP端：APP首页下方“我的”—右上角“设置”—“注销账号”。</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">（六）获取个人信息副本</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">如您想了解您在回收猿平台留存的个人信息副本情况，您可以通过本条第一款所述个人信息“查询”的路径，进入您的个人信息详情页，然后将信息自行复制获取。</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">（七）响应您的上述请求</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">为保障安全，您可能需要提供书面请求，或以其他方式证明您的身份。我们可能会先要求您验证自己的身份，然后再处理您的请求。</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">八、我们如何处理未成年人的个人信息</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">1、回收猿主要为生活服务类平台，我们的服务主要面向成年人（原则上18周岁以上为成年人，16周岁以上且以自己的劳动收入为主要生活来源的我们亦视为成年人）。若您是未成年人，在使用我们的产品和/或服务前，您应在监护人的陪同下阅读本隐私政策，并应确保已征得您的监护人同意后使用我们的服务并向我们提供您的信息。
				我们会根据国家相关法律法规的规定着重保护未成年人的个人信息。</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">2、如您的监护人不同意您按照本隐私政策使用我们的服务或向我们提供信息，请您立即终止使用我们的服务并及时通知我们。</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">3、对于经父母或法定监护人同意而收集未成年人个人信息的情况，我们只会在受到法律允许、父母或监护人明确同意或者保护未成年人所必要的情况下使用或公开披露此信息。</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">4、若您是未成年人的监护人，当您对您所监护的未成年人使用我们的服务或其向我们提供的用户信息有任何疑问时，请您及时与我们联系。我们将根据国家相关法律法规及本隐私政策的规定保护未成年人用户信息的保密性及安全性。如果我们发现自己在未事先获得可证实的父母或法定监护人同意的情况下收集了未成年人的个人信息，则会设法尽快删除相关数据。
			</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">九、本隐私政策的更新和通知</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">我们的隐私政策可能变更。未经您明确同意，我们不会削减您按照本隐私政策所应享有的权利。我们会在本页面上发布对本隐私政策所做的任何变更。</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">对于重大变更，我们还会提供更为显著的通知（包括对于某些服务， 我们会通过电子邮件发送通知，说明隐私政策的具体变更内容）。</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">本隐私政策所指的重大变更包括但不限于：</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">1、我们的服务模式发生重大变化。如处理个人信息的目的、处理的个人信息类型、个人信息的使用方式等；</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">2、我们在所有权结构、组织架构等方面发生重大变化。如业务调整、破产并购等引起的所有者变更等；</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">3、个人信息共享、转让或公开披露的主要对象发生变化；</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">4、您参与个人信息处理方面的权利及其行使方式发生重大变化；</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">5、我们负责处理个人信息安全的责任部门、联络方式及投诉渠道发生变化时；</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">6、个人信息安全影响评估报告表明存在高风险时。</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">您除了在回收猿平台进行注册时，可以查看到最新版本的隐私政策外，PC端，您可以在回收猿首页下方查看到我们最新版本的《回收猿隐私政策》；APP端，您可以在“我的”-“设置”查看最新版本的《回收猿隐私政策》。</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">十、如何联系我们</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">如您对本隐私政策有任何疑问或您在使用我们提供的服务时个人信息受到了侵扰，您可以通过回收猿网站（www.jiadianhuishou.com）首页下方的违法信息举报电话咨询，我们设立了个人信息保护专职部门，将尽快给予您答复。一般情况下，我们将在十五天内（国家法定节假日顺延）回复。我们的联系地址：湛江开发区绿华路48号华都汇7号综合楼1108号办公室，注册地址：湛江开发区绿华路48号华都汇7号综合楼1108号办公室；常用办公地址：湛江开发区绿华路48号华都汇7号综合楼1108号办公室。
			</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">十一、争议解决</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">1、如果您对我们的回复不满意，特别是我们的个人信息处理行为损害了您的合法权益，您还可以向网信办及工商等监管部门进行反馈，其中，中央网信办违法和不良信息举报中心的官方网址为：https://www.12377.cn。
			</text>
			<text class="text-item">&nbsp;</text>
			<text class="text-item">2、因本隐私政策以及我们处理您个人信息事宜引起的任何争议，任何一方可向原告所在地的法院进行诉讼。</text>

		</view>
